
import { mapState } from 'vuex'
import Msg from 'vux/src/components/msg/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import share from '../../components/share/index'
export default {
directives: {
TransferDom
},
components: {
Msg,
share
},
name: 'map',
data () {
let self = this;
return {
icon: 'success',
description: '购买成功',
title: '操作成功',
buttons: [
{
text: '分享给好友',
type: 'primary',
onClick: function () {
self.$refs.share.showBox = true;
}
},{
text: '返回首页',
type: 'default',
link: '/'
},
],
sharePath: ''
}
},
computed: {
...mapState({
openid: state => state.vux.openid,
}),
},
activated: function(){
var sharePath = this.$route.query.sharePath
if(this.$route.query.title) this.title = this.$route.query.title
if(this.$route.query.description) this.description = this.$route.query.description
if(this.sharePath) this.sharePath = sharePath
this.setWxShare('推荐一个比较不错的新玩意给你，快来试试', '小矮人新服务上线，邀您使用。', 'http://xcxcgj.gotomore.cn/index.html#/'+sharePath)
},
created: function() {

},
methods: {
}
}



